<!--  -->
<template>
  <div class="test">
    <h2 style="text-align: center; color: rgb(255, 255, 255);height:0.3rem;line-height: 0.5rem;"></h2>
    <div class="chart">
      <div id="DisChart" :style="{ height: '3rem' }"></div>
    </div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import { onMounted, onUnmounted } from "vue";
export default {
  setup() {
    /// echart
    let echart = echarts;

    onMounted(() => {
      initChart();
    });

    onUnmounted(() => {
      echart.dispose;
    });

    // Echarts
    function initChart() {
      let chart = echart.init(document.getElementById("DisChart"));
      // 
      chart.setOption({
        legend: {
          top: "5%",
          textStyle: {
            color: "white",
          },
        },
        dataset: {
          source: [
            ["location", "A", "B", "C", "D"],
            ["A", 43, 85, 93, 55],
            ["B", 83, 73, 55, 52],
            ["C", 86, 65, 82, 51],
            ["D", 72, 53, 39, 45],
          ],
        },
        xAxis: {
          type: "category",
          axisLabel: {
            textStyle: {
              color: "white",
            },
          },
        },
        yAxis: {
          axisLine: {
            lineStyle: {
              color: "#FFF",
              width: 1,
            },
          },
          axisLabel: {
            textStyle: {
              color: "white",
            },
          },
          splitLine: {
            lineStyle: {
              color: '#2F2F4F',
            },
          },
        },

        series: [
          {
            type: "bar",
            label: {
              show: true,
              position: "top",
              color: "white",
            },
          },
          {
            type: "bar",
            label: {
              show: true,
              position: "top",
              color: "white",
            },
          },
          {
            type: "bar",
            label: {
              show: true,
              position: "top",
              color: "white",
            },
          },
          {
            type: "bar",
            label: {
              show: true,
              position: "top",
              color: "white",
            },
          },
        ],
      });
      window.onresize = function () {
        //
        chart.resize();
      };
    }

    return { initChart };
  },
};
</script>

<style scoped>
.chart {
  margin-top: 0.125rem;
}
</style>
