<!-- A -->
<template>
  <div class="test">
    <h2 style="text-align: center; color: rgb(255, 255, 255);height:0.3rem;line-height: 0.5rem;">A</h2>
    <div class="chart">
      <div id="HChart" :style="{ height: '3rem' }"></div>
    </div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import { onMounted, onUnmounted } from "vue";

export default {
  setup() {
    /// echart
    let echart = echarts;

    onMounted(() => {
      initChart();
    });

    onUnmounted(() => {
      echart.dispose;
    });

    // Echarts
    function initChart() {
      let chart = echart.init(document.getElementById("HChart"));
      // 
      chart.setOption({
        title: {
          text: ": 99.1%           : 99.3%             : 99%              : 100%",
          x:"13%",
          y:"70%",
          textStyle:{
            color: "white",
            fontSize: 12
          }
        },
        legend: {
          top: "2%",
          textStyle: {
            color: "white",
          },
        },
        color: ["#70DBDB", "#B87333"],
        dataset: {
          source: [
            ["location", "", ""],
            ["A", 540, 5],
            ["B", 300, 2],
            ["C", 100, 1],
            ["D", 50, 0],
          ],
        },
        xAxis: {
          name: "",
          type: "category",
          axisLine: {
            lineStyle: {
              color: "#FFF",
              width: 1,
            },
          },
        },
        yAxis: {
          name: "",
          axisLine: {
            lineStyle: {
              color: "#FFF",
              width: 1,
            },
          },
          splitLine: {
            lineStyle: {
              color: '#2F2F4F',
            },
          },

        },
        grid:{ show:false, top:'20%',bottom:'40%' },

        series: [
          {
            type: "bar",
            barWidth: "25%",

            label: {
              show: true,
              color: "white",
              position: "top",
            },
          },
          {
            type: "bar",
            barWidth: "25%",
            label: {
              show: true,
              color: "white",
              position: "top",
            },
          },
        ],
      });
      window.onresize = function () {
        //
        chart.resize();
      };
    }

    return { initChart };
  },
};
</script>

<style scoped>
.chart {
  margin-top: 0.125rem;
}
</style>
